import {
  backdrop,
  closeButton,
  openButton,
  form,
  emailInput,
  passwordInput,
  signUpBtn,
  signInBtn,
  linkUp,
  linkIn,
  nameInput,
  signUpCheckbox,
  userLogout,
  logOutBtn,
  userbar,
  regIGree,
} from './registration/reg-const';

//  Firebase
const firebaseConfig = {
  apiKey: 'AIzaSyDMnXjhWjIMeE1EJ6KOnyUAYjgWeRJevIU',
  authDomain: 'project-techno12-public.firebaseapp.com',
  projectId: 'project-techno12-public',
  storageBucket: 'project-techno12-public.appspot.com',
  messagingSenderId: '353320685234',
  appId: '1:353320685234:web:1ac20ad763e02539e643ab',
  measurementId: 'G-P3W3NGWD78',
};

//  Firebase
const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);
const auth = getAuth(app);

//     
const showModal = () => {
  backdrop.classList.toggle('visually-hidden');
  signInBtn.style.display = 'none';
};

//     
const hideModal = () => {
  backdrop.classList.toggle('visually-hidden');
};

//          
closeButton.addEventListener('click', hideModal);
openButton.addEventListener('click', showModal);

//           
backdrop.addEventListener('click', event => {
  if (event.target === backdrop) {
    hideModal();
  }
});

//            "Escape"
document.addEventListener('keydown', event => {
  if (event.key === 'Escape') {
    hideModal();
  }
});

//        
const toggleClassBtnLinkUp = () => {
  signUpBtn.style.display = 'block';
  signInBtn.style.display = 'none';
  nameInput.style.display = 'block';
  regIGree.style.display = 'flex';
};

const toggleClassBtnLinkIn = () => {
  signUpBtn.style.display = 'none';
  nameInput.style.display = 'none';
  signInBtn.style.display = 'block';
  regIGree.style.display = 'none';
};

//      
linkUp.addEventListener('click', toggleClassBtnLinkUp);
linkIn.addEventListener('click', toggleClassBtnLinkIn);

//           
signUpBtn.addEventListener('click', validateForm);

//    
function validateForm(event) {
  event.preventDefault();
  const emailValue = emailInput.value;
  const passwordValue = passwordInput.value;
  const emailPattern = /\S+@\S+.\S+/;

  let formIsValid = true;

  //    
  if (!emailValue || !passwordValue) {
    Notiflix.Notify.failure(' ,   !');
    emailInput.classList.add('red_border');
    passwordInput.classList.add('red_border');
    formIsValid = false;
  }

  //      
  if (!emailPattern.test(emailValue)) {
    Notiflix.Notify.failure(
      ' ,     !'
    );
    emailInput.classList.add('red_border');
    formIsValid = false;
  } else {
    emailInput.classList.remove('red_border'); //       
  }

  //    
  if (passwordValue.length < 6) {
    Notiflix.Notify.failure('    6 !');
    passwordInput.classList.add('red_border');
    formIsValid = false;
  } else {
    passwordInput.classList.remove('red_border'); //      
  }

  //  ,   
  if (formIsValid) {
    createUserWithEmailAndPassword(auth, emailValue, passwordValue)
      .then(userCredential => {
        const user = userCredential.user;
        hideModal();
        Notiflix.Notify.success('!   !');
        toggleClassBtnLinkIn(); //    

        //       
        emailInput.value = '';
        passwordInput.value = '';
      })
      .catch(error => {
        const errorCode = error.code;
        const errorMessage = error.message;
        Notiflix.Notify.failure(errorMessage);
      });

    hideModal();
    Notiflix.Notify.success('!   !');

    //       
    emailInput.value = '';
    passwordInput.value = '';
  }
}
//    
function signInUser() {
  const emailValue = emailInput.value;
  const passwordValue = passwordInput.value;

  signInWithEmailAndPassword(auth, emailValue, passwordValue)
    .then(userCredential => {
      const user = userCredential.user;
      //     
      // ,            .
      console.log(' :', user);
      backdrop.style.display = 'none';
    })
    .catch(error => {
      const errorCode = error.code;
      const errorMessage = error.message;
      Notiflix.Notify.failure(errorMessage);
    });
}

//           
signInBtn.addEventListener('click', signInUser);

// ,       
auth.onAuthStateChanged(user => {
  if (user) {
    //   ()
    console.log(' :', user);
    openButton.style.display = 'none';
    userbar.style.display = 'block';
  } else {
    //   ()
    console.log(' ');
    userbar.style.display = 'none';
  }
});

//      
signUpCheckbox.addEventListener('change', function () {
  if (this.checked) {
    signUpBtn.removeAttribute('disabled');
  } else {
    signUpBtn.setAttribute('disabled', 'disabled');
  }
});

//       
userLogout.addEventListener('click', () => {
  signOut(auth)
    .then(() => {
      //     
      console.log(' ');
      location.reload(); //  
    })
    .catch(error => {
      //   
      console.error(' :', error);
    });
});
